<template>
  <div class="myPlayer">
    <div class="myVideoBox">
      <h3>我的小视频</h3>
      <video-player
        class="video-player-box vjs-big-play-centered"
        ref="videoPlayer"
        v-if="showBox"
        :options="playerOptions"
        :playsinline="true"
        customEventName="customstatechangedeventname"
        @play="onPlayerPlay($event)"
        @pause="onPlayerPause($event)"
        @ended="onPlayerEnded($event)"
        @waiting="onPlayerWaiting($event)"
        @playing="onPlayerPlaying($event)"
        @loadeddata="onPlayerLoadeddata($event)"
        @timeupdate="onPlayerTimeupdate($event)"
        @canplay="onPlayerCanplay($event)"
        @canplaythrough="onPlayerCanplaythrough($event)"
        @statechanged="playerStateChanged($event)"
        @ready="playerReadied($event)"
      >
      </video-player>
      <div class="myVideoList">
        <el-row
          class="item"
          v-for="(i,index) in playerOptions.sources"
          :key="index"
          @click="changeVideo(i,index)"
        >
          <el-col
            :span="24"
            class="ov-1"
          >{{index}}&nbsp;{{i.title}}</el-col>
        </el-row>
      </div>
    </div>
  </div>

</template>

<script>
// Similarly, you can also introduce the plugin resource pack you want to use within the component
// import 'some-videojs-plugin'
export default {
  data() {
    return {
      showBox: false,
      playerOptions: {
        playbackRates: [0, 5, 1.0, 1.5, 2.0],
        // 如果为true,浏览器准备好时开始回放。
        autoplay: false,
        // 默认情况下将会消除任何音频。
        muted: false,
        // 是否视频一结束就重新开始。
        loop: false,
        // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        preload: "auto",
        language: "zh-CN",
        // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        aspectRatio: "16:9",
        // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        fluid: true,
        sources: [
          {
            type: "video/mp4",
            src:
              "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm",
            poster:
              "https://cn.bing.com/th?id=OHR.WatkinsGlen_EN-US1837020817_1920x1080.jpg&amp;rf=LaDigue_1920x1080.jpg&amp;pid=hp",
            title: "示例"
          }
        ],
        poster: "", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        notSupportedMessage: "此视频暂无法播放，请稍后再试",
        controlBar: {
          // timeDivider: true, // 当前时间和持续时间的分隔符
          // durationDisplay: true, // 显示持续时间
          // remainingTimeDisplay: false, // 是否显示剩余时间功能
          // fullscreenToggle: true, // 是否显示全屏按钮
          // volumePanel: {
          //   inline: false
          // }, //声音样式
          // currentTimeDisplay:true,//时间长度
          /* 
          使用children的形式可以控制每一个控件的位置，以及显示与否 
          playToggle, // 播放暂停按钮
          volumeMenuButton,// 音量控制
          currentTimeDisplay,// 当前播放时间
          timeDivider, // '/' 分隔符
          durationDisplay, // 总时间
          progressControl, // 点播流时，播放进度条，seek控制
          liveDisplay, // 直播流时，显示LIVE
          remainingTimeDisplay, // 当前播放时间
          playbackRateMenuButton, // 播放速率，当前只有html5模式下才支持设置播放速率
          fullscreenToggle // 全屏控制
          */
          children: [
            { name: "playToggle" }, // 播放按钮
            { name: "currentTimeDisplay" }, // 当前已播放时间
            { name: "progressControl" }, // 播放进度条
            { name: "durationDisplay" }, // 总时间
            {
              // 倍数播放
              name: "playbackRateMenuButton",
              playbackRates: [0.5, 1, 1.5, 2, 2.5],
              inline: true // 不使用水平方式
            },
            {
              name: "volumePanel", // 音量控制
              inline: true // 不使用水平方式
            },
            { name: "FullscreenToggle" } // 全屏
          ]
        }
      }
    };
  },
  mounted() {
    const loading = this.$loading({
      lock: true,
      text: "加载中请稍后",
      spinner: "el-icon-loading",
      background: "rgba(199, 203, 189, 1)"
    });
    setTimeout(() => {
      this.showBox = true;
      loading.close();
    }, 2000);
    this.playerOptions.sources = this.playerOptions.sources.concat(
      this.baseData.video
    );
    this.playerOptions.poster = this.playerOptions.sources[0].poster;
    this.playerOptions.sources[0].src = this.playerOptions.sources[0].src;
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    }
  },
  methods: {
    changeVideo(i, index) {
      this.playerOptions.poster = this.playerOptions.sources[index].poster;
      this.playerOptions.sources[0].src = this.playerOptions.sources[index].src;
    },
    // listen event
    onPlayerPlay(player) {
      // console.log('player play!', player)
    },
    onPlayerPause(player) {
      // console.log('player pause!', player)
    },
    onPlayerEnded(player) {
      // console.log('player ended!', player)
    },
    onPlayerLoadeddata(player) {
      // console.log('player Loadeddata!', player)
    },
    onPlayerWaiting(player) {
      // console.log('player Waiting!', player)
    },
    onPlayerPlaying(player) {
      // console.log('player Playing!', player)
    },
    onPlayerTimeupdate(player) {
      // console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay(player) {
      // console.log('player Canplay!', player)
    },
    onPlayerCanplaythrough(player) {
      // console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged(playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied(player) {
      // seek to 10s
      console.log("example player 1 readied", player);
      // player.currentTime(10)
      // console.log('example 01: the player is readied', player)
    }
  }
};
</script>
<style lang="scss">
.myPlayer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
  background: rgb(27, 27, 27);
  padding-top: 12px;
  box-sizing: border-box;
}
.myVideoBox {
  position: relative;
  width: 100%;
  height: 538px;
  text-align: center;
  overflow: hidden;
}
.video-player-box {
  width: 96%;
  height: 538px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin: auto;
}
.myVideoList {
  position: absolute;
  top: 39px;
  right: 0;
  width: 120px;
  height: 538px;
  overflow: auto;
  .item {
    width: 100%;
    height: 24px;
    text-align: left;
    font-size: 12px;
    padding: 6px 10px 4px 4px;
    cursor: pointer;
  }
}
.myVideoList::-webkit-scrollbar {
  display: none;
}
.vjs-custom-skin > .video-js .vjs-big-play-button {
  transform: scale(0.6) !important;
}

@media screen and (max-width: 414px) {
  .video-player-box {
    margin-top: 12px;
    height: 179px;
  }
  .myVideoList {
    height: 177px;
    color: #ccc;
  }
}
</style>




